/* III. Default styles
 * --------------------------------------- */
/* 1. Basic
 * ------------------- */
html {
  font-family: "Winston", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 10px;
}

h1, h2, h3, h4, h5, h6,
p,
.section {
  cursor: default;
}

.container, .container_fluid, .container_xl, .container_lg, .container_md, .container_sm {
  margin: 0 auto;
  padding: 0 1.5rem;
}

.container {
  max-width: 117rem;
}

.container_lg {
  max-width: 96rem;
}

.container_md {
  max-width: 72rem;
}

.container_sm {
  max-width: 54rem;
}

.flex-none {
  -webkit-box-flex: 0;
  -ms-flex: none;
  flex: none;
}

.section__heading, .section__subheading {
  text-align: center;
}

.section__subheading {
  font-size: 2rem;
}

.row {
  margin: 0 -1.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.col {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}

.m-x-0 {
  margin-left: 0;
  margin-right: 0;
}

.scroll-hidden {
  /*для скрытия полосы прокрутки*/
  padding-right: 17px;
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  max-width: 1920px;
  overflow: hidden;
  position: relative;
}

/* 2. Typography
 * -------------------------*/
address {
  font-style: normal;
}

body {
