/* 3. Links
 * -------------------------*/
a, a:visited {
  text-decoration: none;
  outline: none;
  -webkit-transition: color 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}

a.text-link, a:visited.text-link {
  text-decoration: underline;
}

a.text-link:hover, a.text-link:focus {
  text-decoration: none;
}

a.text-link:active {
  text-decoration: none;
}

/* 4. Images
 * -------------------------*/
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* 5. Buttons
 * -------------------------*/
button,
input[type=submit],
input[type=reset],
input[type=button],
.btn {
  font-family: "Winston";
  text-decoration: none;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.btn {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background: none;
  overflow: hidden;
}

.btn__text {
  margin-left: 1.2rem;
}

.btn_full-width, .btn_lg, .btn_md, .btn_sm {
  width: 100%;
}

.btn_lg {
  max-width: 20rem;
}

.btn_md {
  max-width: 15rem;
}

.btn_sm {
  max-width: 10rem;
}

.btn_primary {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  position: relative;
  z-index: 1;
  padding: 0.5em;
  width: 27rem;
  height: 8rem;
  font-size: 1.6rem;
  font-weight: 400;
  color: #fff;
  border-radius: 0.5rem;
  background: -webkit-gradient(linear, left bottom, left top, from(#002a94), to(#1648c5));
  background: -o-linear-gradient(bottom, #002a94, #1648c5);
  background: linear-gradient(0deg, #002a94, #1648c5);
  -webkit-box-shadow: inset 1px 1px 2px 1px rgba(255, 255, 255, 0.5);
  box-shadow: inset 1px 1px 2px 1px rgba(255, 255, 255, 0.5);
  overflow: visible;
}

.btn_primary::after {
  position: absolute;
  top: 4.5rem;
  bottom: 0;
  z-index: -1;
  width: 55%;
  height: 0;
  content: "";
  background: #1648c5;
  -webkit-box-shadow: 0px 0px 65px 35px #1648c5;
  box-shadow: 0px 0px 65px 35px #1648c5;
}

.btn_primary:hover {
  background: -webkit-gradient(linear, left bottom, left top, from(#0031ae), to(#1950dc));
  background: -o-linear-gradient(bottom, #0031ae, #1950dc);
  background: linear-gradient(0deg, #0031ae, #1950dc);
}

.btn_primary:hover::after {
  -webkit-box-shadow: 0px 0px 75px 35px #1648c5;
  box-shadow: 0px 0px 75px 35px #1648c5;
}

.btn_primary:active, .btn_primary:focus {
  background: -webkit-gradient(linear, left bottom, left top, from(#00237b), to(#275ee7));
  background: -o-linear-gradient(bottom, #00237b, #275ee7);
  background: linear-gradient(0deg, #00237b, #275ee7);
  z-index: 1;
}

.btn_primary:active::after, .btn_primary:focus::after {
  width: 70%;
  top: 5rem;
  -webkit-box-shadow: 5px 11px 40px 21px #1648c5;
  box-shadow: 5px 11px 40px 21px #1648c5;
}

.btn_primary:disabled {
  background: -webkit-gradient(linear, left bottom, left top, from(#2c3d68), to(#425b99));
  background: -o-linear-gradient(bottom, #2c3d68, #425b99);
  background: linear-gradient(0deg, #2c3d68, #425b99);
}

.btn_primary:disabled::after {
  -webkit-box-shadow: 0px 0px 75px 30px #425b99;
  box-shadow: 0px 0px 75px 30px #425b99;
}

.btn_primary:disabled:active {
  -webkit-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px);
}

.btn_primary:disabled:active::after {
  -webkit-box-shadow: 0px 0px 75px 25px #425b99;
  box-shadow: 0px 0px 75px 25px #425b99;
  width: 55%;
}

.btn_secondary {
  padding: 0.5em;
  width: 32.5rem;
  height: 9.5rem;
  font-size: 1.6rem;
  font-weight: 500;
  color: #000000;
  background: #ffffff;
  border-radius: 0.5rem;
  -webkit-box-shadow: 13px 13px 32px rgba(0, 0, 0, 0.33);
  box-shadow: 13px 13px 32px rgba(0, 0, 0, 0.33);
}

.btn_secondary:hover {
  background: #ededed;
}

.btn_secondary:active {
  -webkit-transform: translateY(1px);
  -ms-transform: translateY(1px);
  transform: translateY(1px);
  -webkit-box-shadow: 7px 7px 20px rgba(0, 0, 0, 0.33);
  box-shadow: 7px 7px 20px rgba(0, 0, 0, 0.33);
}

