.header,
.intro,
.intro-2 {
  color: #ffffff;
}

/* Header 
 * ------------------------- */
.header {
  position: absolute;
  top: 7.5rem;
  left: 0;
  right: 0;
  z-index: 10;
  width: 100%;
}

.header__content {
  display: -webkit-box;
